.body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Bad Script',
    cursive;
  font-size: large;
  color: #000;
}

.contentCard h1,
h3 {
  font-family: 'Bad Script',
    cursive;

}


