.containerLogin {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  flex-wrap: wrap;
  z-index: 1;
}

label {
  margin: 2%;

}

input {
  width: 100%;
}

span,
button {
  padding: 5px;
}

button:hover,
span:hover {
  color: rgb(36, 83, 50);
}



.cardA {
  width: 800px;
  height: 580px;
  font-family: 'Bad Script',
    cursive;
}

.cardS {
  width: 350px;
  height: 400px;
}

.contentCard h1 {
  font-weight: 1000;

}

.containerLogin .cardS,
.cardA {
  position: relative;
  margin: 10px;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
}

.containerLogin .cardS .contentCard,
.cardA {
  padding: 25px;
  text-align: center;

}

.containerLogin .cardS .contentCard h2 {
  position: absolute;
  top: -60px;
  right: 30px;
  font-size: 8em;
  color: rgba(255, 255, 255, 0.05);
  pointer-events: none;
}


.containerLogin .cardS .contentCard p {
  font-size: 1em;
  color: #000;
  font-weight: 300;
}
.footer {
  margin-top: 10rem;
  padding: 5px;
  background-color: #023220;
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  margin-bottom: 0;
  left: 0;
  width: 100%;

}

.footer span {
  /* position: fixed; */
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1px;
  background: #B8B8B8;
}

.footer-heading {
  font-family: 'Bad Script',
    cursive;
  font-weight: bold;
  color: white
}

.mission-statement {
  font-family: cursive;
  color: #08f09b
}

.footer-info {
  color: #08f09b;
  font-family: Arial, Helvetica, sans-serif;
}
.card {

  width: 16rem;
  height: 23rem;
  margin: 30px;
  background-color: rgba(24, 247, 128, 0.25);

}

#restaurant-card {
  width: 16rem;
  height: 23rem;
  margin: 30px;
  background-color: rgba(24, 247, 128, 0.25);
}

.image {
  width: 200px;
  height: 200px
}

.card:hover {
  transform: scale(1.1);
}
form.search {
  overflow: auto;
  margin-bottom: 10px;
}

form.search button[type="submit"] {
  float: right;
  margin-top: 5px;
}
.list-group>.list-group {
  display: none;
  margin-bottom: 0;
}

.list-group-item:focus-within+.list-group {
  display: block;
}

.list-group>.list-group-item {
  border-radius: 0;
  border-width: 1px 0 0 0;
}

.list-group>.list-group-item:first-child {
  border-top-width: 0;
}

.list-group>.list-group>.list-group-item {
  padding-left: 2.5rem;
}

.list-group-collapse {
  overflow: hidden;
}
