}

.map {
  width: 80vw;
  height: 50vh;
  position: relative;
}

.location-info {
  position: absolute;
  top: 100px;
  left: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: rgb(216, 199, 70);
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}
